"use client";

import React from "react";
import Local from "next/font/local";
import ReactPlayer from "react-player";
import { AiOutlineExclamationCircle } from "react-icons/ai";
import Image from "next/image";
import SparkleButton from "../ButtonForMobileView/SparkleButton";
import { useEffect, useState } from "react";
import { IoArrowUndo } from "react-icons/io5";
import { div } from "framer-motion/client";

const heroes = Local({
  src: [
    {
      path: "../../../public/fonts/Heroes Legend.ttf",
      weight: "400",
      style: "normal",
    },
  ],
  variable: "--font-heroes-legend",
  display: "swap",
});

const superMarker = Local({
  src: [
    {
      path: "../../../public/fonts/SuperBlackMarker.ttf",
      weight: "600",
      style: "normal",
    },
  ],
  variable: "--font-superMarker",
  display: "swap",
});

const wingly = Local({
  src: [
    {
      path: "../../../public/fonts/SuperBlackMarker.ttf",
      weight: "600",
      style: "normal",
    },
  ],
  variable: "--font-wingly",
  display: "swap",
});


interface Project {
  projectId: string;
  buttonUrl?: string;
  videoUrl?: string;
  title: string;
  thumbnail: string;
  description: string;
  shortDescription?: string;
  link?: string;
  linkText?: string;
  date: string;
  lastUpdated?: undefined;
}

interface ProjectLinks {
  [key: string]: string;
}


const DescriptionBg = "https://res.cloudinary.com/dmmzqpfgg/image/upload/v1749833128/orange-comic-background_692379-312_xvpzbm.jpg";

const ProjectDetails = ({ project }: { project: Project }) => {
  const [codesnapLink, setCodesnapLink] = useState<ProjectLinks | null>(null);

  useEffect(() => {
    const fetchLinks = async () => {
      try {
        const response = await fetch(
          "https://api.jsonbin.io/v3/b/67ed76908561e97a50f7853a",
          {
            headers: {
              "X-Master-Key":
                "$2a$10$8lN9jglX2MyremXQgBZ1AuSxtjqHrxfbmSJcfa4eLCdNfhgEdXSt.",
              "Content-Type": "application/json",
            },
          }
        );
        const data = await response.json();
        setCodesnapLink(data.record.CodeSnapAppLinks);
      } catch (error) {
        console.error("Failed to fetch links:", error);
        setCodesnapLink({
          download:
            "https://github.com/The-Dev-Sumit/CodeSnapApp/releases/download/MyApp/CodeSnap-1-v-win-x64.7z",
        }
        );
      }
    };

    fetchLinks();
  }, []);

  const EndInfo = "https://res.cloudinary.com/dmmzqpfgg/image/upload/v1750007402/comic-book-cartoon-speech-bubble-text-puff-cloud-green-background-template-pop-art-dialog-conversation-funny-smoke-steam-187352782_yfjti7.jpg"

  return (
    <div className="w-full min-h-screen">
        <button 
        onClick={() => window.history.back()}
        title="Back"
        className="px-2 py-1"
        >
          <IoArrowUndo size={30} color="yellow"/>
        </button>
      <div className="w-full h-[23rem] flex flex-col space-y-3">
        <div className="py-2 w-full mt-6">
          <h1
            className={`text-xl tracking-wide text-shadow-2xs leading-9 text-shadow-black text-amber-300 font-bold w-full text-center ${heroes.className}`}>
            {project.title}
          </h1>
        </div>
        <div className="w-full px-3 flex flex-row justify-between font-semibold text-amber-300 text-shadow-2xs text-shadow-black">
          <p>{project.lastUpdated}</p>
          <p>{project.date}</p>
        </div>
        <div className="w-full flex items-start space-x-1 font-semibold tracking-wider text-white opacity-90 px-5 mt-3">
          <AiOutlineExclamationCircle size={15} />
          <p className="text-sm w-full leading-4">
            Note: These videos were made just for fun with my batchmates.
          </p>
        </div>
        <div className="w-full h-[11.3rem] rounded-2xl px-2">
          <ReactPlayer
            url={project.videoUrl}
            width="100%"
            height="100%"
            controls
            playing={false}
            light={project.thumbnail}
          />
        </div>
      </div>
      <div className="w-full rounded-lg relative flex flex-col items-center justify-center mt-5">
        <p className={`absolute z-20 px-4 ${superMarker.className} text-red-700 text-shadow-xs text-shadow-black tracking-wider`}>
          {project.description}
        </p>
        <div className="h-[11.3rem] w-full relative border-2 border-t-red-800 border-b-transparent ">
          <Image src={DescriptionBg} alt="comic style bg" fill />
      </div>
      </div>
      <div className="w-full flex items-center justify-center mt-5 py-5">
        <SparkleButton href={
    project.link
      ? project.link
      : project.projectId === "codesnap-software"
        ? codesnapLink?.download
        : undefined
  }
        text={project.linkText || "View Project"}
        />
      </div>
      { project.projectId === "codesnap-software" && (
        <div className="mt-10 flex flex-col items-center justify-center relative">
          <p className="absolute z-20 px-16 text-center">This is for Windows, and the software you download from the download button will be the latest version, for Windows 10 and above</p>
          <div className="h-[11.3rem] w-full relative border-2 border-t-red-800 border-b-transparent ">
          <Image src={EndInfo} alt="comic style bg" fill />
      </div>
        </div>
      )}
    </div>
  );
};

export default ProjectDetails;
